}
.button_3
{
	border: solid 2px transparent;
	-webkit-border-image: -webkit-gradient(linear, left top, right top, from(#4867c0), to(#329fec));
	-webkit-border-image: linear-gradient(to right, #4867c0, #329fec);
	        border-image: -webkit-gradient(linear, left top, right top, from(#4867c0), to(#329fec));
	        border-image: linear-gradient(to right, #4867c0, #329fec);
	border-image-slice: 1;
}
.button_3::before
{
	background: transparent;
	opacity: 0;
	-webkit-transition: all 200ms ease;
	transition: all 200ms ease;
}
.button_3 a
{
	color: #4c4c4c;
}
.button_3:hover::before
{
	opacity: 1;
	background-color: #4867c0;
	background-image: -webkit-gradient(linear, left top, right top, from(#4867c0), to(#329fec));
	background-image: linear-gradient(to right, #4867c0, #329fec);
	background: #4867c0;
	background: -webkit-gradient(linear, left top, right top, from(#4867c0), to(#329fec));
	background: linear-gradient(to right, #4867c0, #329fec);
}
.button_3:hover a
{
	color: #FFFFFF;
}

/*********************************
5.2 Elements - Accordions
*********************************/

.elements_accordions_tabs
{
	margin-top: 115px;
