.elements_accordions
{
	width: 100%;
}
.accordions_tabs
{
	width: 100%;
	background: #FFFFFF;
	margin-top: 100px;
}
.accordions
{
	margin-top: 115px;
}
.accordion_container:not(:last-child)
{
	margin-bottom: 14px;
}
.accordion
{
	height: 57px;
	width: 100%;
	background: #f0f2f6;
	padding-left: 42px;
	cursor: pointer;
	color: #888888;
	font-size: 16px;
	font-weight: 500;
	-webkit-transition: all 200ms ease;
	transition: all 200ms ease;
}
.accordion div
{
	max-width: 90%;
	overflow: hidden;
	white-space: nowrap;
}
.accordion::after
{
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	position: absolute;
	top: 50%;
	-webkit-transform: translateY(-50%);
	transform: translateY(-50%);
	right: 12px;
	content: '+';
	font-size: 16px;
	color: #888888;
	font-weight: 600;
	-webkit-transition: all 200ms ease;
	transition: all 200ms ease;
}
.accordion.active
{
	background-color: #4867c0;
	background-image: -webkit-gradient(linear, left top, right top, from(#4867c0), to(#329fec));
	background-image: linear-gradient(to right, #4867c0, #329fec);
	background: #4867c0;
	background: -webkit-gradient(linear, left top, right top, from(#4867c0), to(#329fec));
	background: linear-gradient(to right, #4867c0, #329fec);
	color: #FFFFFF;
}
.accordion.active::after
{
	content: '-';
	color: #FFFFFF;
}
.accordion_panel
{
	padding-right: 8px;
	max-height: 0px;
	overflow: hidden;
	-webkit-transition: all 500ms ease;
	transition: all 500ms ease;
}
.accordion_panel > div
{
	padding-bottom: 11px;
}
.accordion_panel p
{
	font-size: 14px;
	font-weight: 500;
	padding-top: 21px;
	color: #888888;
	line-height: 2.14;
}

