{
	width: 100%;
	padding-top: 57px;
	padding-bottom: 57px;
}
.news_image_container
{

}
.news_image
{
	width: 100%;
}
.news_image img
{
	max-width: 100%;
}
.date_container
{
	position: absolute;
	top: 24px;
	left: 24px;
}
.date_container a
{
	display: block;
	position: relative;
}
.date_content
{
	width: 91px;
	height: 91px;
	background-color: #4867c0;
	background-image: -webkit-gradient(linear, left top, right top, from(#4867c0), to(#329fec));
	background-image: linear-gradient(to right, #4867c0, #329fec);
	background: #4867c0;
	background: -webkit-gradient(linear, left top, right top, from(#4867c0), to(#329fec));
	background: linear-gradient(to right, #4867c0, #329fec);
}
.date_day
{
	font-size: 30px;
	font-weight: 500;
	color: #FFFFFF;
	line-height: 0.75;
}
.date_month
{
	font-size: 18px;
	font-weight: 500;
	color: #FFFFFF;
	line-height: 0.75;
	margin-top: 10px;
}
.news_body
{
	padding-top: 32px;
}
.news_title a
{
	font-size: 30px;
	font-weight: 500;
	color: #4c4c4c;
	line-height: 1.2;
}
.news_info
{
	margin-top: 8px;
}
.news_info ul li
{
	display: inline-block;
	position: relative;
